<template>
    <div class="users-wrapper" v-show="isOpen">
        <div @click="closeProfiles" class="shade" v-bind:class="[isOpen ? 'fadeIn' : '' , isClosing ? 'fadeOut' : 'fadeIn']"></div>
        <div class="side-container" v-bind:class="[isOpen ? 'slideLeft' : '' , isClosing ? 'slideRight' : '']">
            <h5>YOUR PROFILE</h5>
            <div class="user-container" @click="openProfile(user)">
                <div class="img-box">
                    <img v-if="user.image" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user.image" alt=""
                        v-show="user.image">
                    <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="!user.image">
                </div>
                <div class="user-name">
                    {{user.name}}
                </div>
            </div>
            <h5>MATCHED PROFILES</h5>
            <div class="user-container" v-for="user1 in matched" @click="openProfile(user1)">
                <div class="img-box">
                    <img v-if="user.image" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user1.image" alt=""
                        v-show="user1.image">
                    <img src="http://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="!user1.image">
                </div>
                <div class="user-name">
                    {{user1.name}}
                </div>
            </div>

        </div>
    </div>
</template>
<script>
    import {
        Bus
    } from '../main'
    import {
        mapGetters
    } from 'vuex';

    export default {
        name: 'userprofiles',
        data() {
            return {
                isOpen: false,
                isClosing: false
            }
        },
        computed: {
            ...mapGetters({
                matched: 'matched',
                messages: 'messages',
                user: 'user',
                newMessages: 'newMessages'
            }),

        },
        mounted: function () {
            this.isClosing = false
            Bus.$on('openProfiles', () => {
                this.isOpen = true

            });
            Bus.$on('closeProfiles', () => {
                this.isClosing = true
                setTimeout(() => {
                    this.isClosing = false
                    this.isOpen = false
                }, 500)
            })
        },
        methods: {
            closeProfiles: function () {
                this.isClosing = true
                setTimeout(() => {
                    this.isClosing = false
                    this.isOpen = false
                }, 500)
            },
            openProfile: function (user) {
                Bus.$emit('openProfile', user)
            }
        }
    }
</script>
<style scoped>
    .users-wrapper {
        position: fixed;
        height: 100%;
        width: 100%;
        z-index: 1000;
    }

    .shade {
        position: fixed;
        height: 100%;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.6);
    }

    .side-container {
        position: fixed;
        height: 100%;
        width: 220px;
        padding: 5px;
        background-color: #fff;
        overflow-y: scroll;
        overflow-x: hidden;
        margin-left: -230px;
    }

    .user-container {
        width: 100%;
        height: 50px;
        border-bottom: 1px solid #eee;
        margin-top: 10px;
    }

    .img-box {
        display: inline-block;
        width: 40px;
        height: 40px;
        overflow: hidden;
        border-radius: 50%;
        background-color: #fff;
    }

    .img-box img {
        width: 100%;
    }

    .user-name {
        height: 100%;
        display: inline-block;
        vertical-align: middle;
        text-align: center;
        text-transform: capitalize;
        font-size: 0.9em;
    }

    h5 {
        font-size: 0.6em;
        color: #999;
    }

    .fadeIn {
        animation-name: fadeIn;
        animation-duration: 0.4s;
        animation-fill-mode: forwards;
    }

    .fadeOut {
        animation-name: fadeOut;
        animation-duration: 0.4s;
        animation-delay: 0.2s;
        animation-fill-mode: forwards;
    }

    .slideLeft {
        animation-name: slideLeft;
        animation-duration: 0.4s;
        animation-delay: 0.2s;
        animation-fill-mode: forwards;
    }

    .slideRight {
        margin-left: 0;
        animation-name: slideRight;
        animation-duration: 0.4s;
        animation-fill-mode: forwards;
    }

    @keyframes fadeIn {
        from {
            opacity: 0
        }
        to {
            opacity: 1
        }
    }

    @keyframes fadeOut {
        from {
            opacity: 1
        }
        to {
            opacity: 0
        }
    }

    @keyframes slideLeft {
        from {
            margin-left: -230px;
        }
        to {
            margin-left: 0
        }
    }

    @keyframes slideRight {
        from {
            margin-left: 0;
        }
        to {
            margin-left: -230px;
        }
    }
</style>



