import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import {
    url1,
    url2,
    Bus
} from './main';
import vueRouter from 'vue-router';

Vue.use(Vuex)
var router = new vueRouter();
export const store = new Vuex.Store({
    state: {
        user: {},
        errors: {
            login: {
                hasError: false,
                message: ''
            },
            user: '',
            registration: ''
        },
        users: [],
        matched: [],
        messages: [],
        chatActive: '',
        newMessages: []
    },
    getters: {
        user(state) {
            return state.user
        },
        messages(state) {
            var newMessages = [];
            if(state.messages.length != 1){
                var messages = state.messages.sort(function (a, b) {
                    var c = new Date(a.created_at)
                    var d = new Date(b.created_at)
                    return c - d
                })
            }else{
                var messages = state.messages
            }
            
            var newMessages = messages.filter((message) => {
                var day = new Date(message.created_at).getDay()

                switch (day) {
                    case 0:
                        message.day = 'sun'
                        break;
                    case 1:
                        message.day = 'mon'
                        break;
                    case 2:
                        message.day = 'tue'
                        break;
                    case 3:
                        message.day = 'wed'
                        break;
                    case 4:
                        message.day = 'thurs'
                        break;
                    case 5:
                        message.day = 'fri'
                        break;
                    case 6:
                        message.day = 'sat'
                        break;

                }

                var hours = new Date(message.created_at).getHours();
                var min = new Date(message.created_at).getMinutes();
                var sec = new Date(message.created_at).getSeconds();
                if (hours < 12) {

                    if (hours == 0) {
                        message.time = '12 :' + min + ':' + sec + ' AM'
                    } else {
                        message.time = hours + ':' + min + ':' + sec + ' AM'
                    }

                } else {

                    if (hours == 12) {
                        message.time = '12 :' + min + ':' + sec + ' PM'
                    } else {
                        message.time = hours - 12 + ':' + min + ':' + sec + ' PM'
                    }

                }

              
                return message
            })

            return newMessages
        },
        loginError(state) {
            return state.errors.login
        },
        users(state) {

            let users = state.users.filter(user => {
                switch (state.user.interests) {
                    case 'men':
                        return user.gender === 'male'
                        break;
                    case 'women':
                        return user.gender === 'female'
                        break;
                    case 'both':
                        return user
                        break;
                }
            })




            if (users.length != 0) {
                for (var i = 0; i < state.user.liked.length; i++) {

                    users = users.filter(user => {
                        return user._id != state.user.liked[i]
                    })

                }
            }


            users = users.filter(user => {
                return user._id != state.user._id
            })

            return users
        },
        matched(state) {
            return state.matched
        },
        newMessages(state) {
            return state.newMessages
        }

    },
    mutations: {

        //SOCKETS
        SOCKET_CONNECT: (state, id) => {
          
        },
        SOCKET_MESSAGECHANNEL: (state, data) => {
         
        },
        SOCKET_ERROR: (state, error) => {
            switch (error.error) {
                case 'login':
                    state.errors.login.hasError = true,
                        state.errors.login.message = error.message
                    setTimeout(() => {
                        state.errors.login.hasError = false
                        state.errors.login.message = ''
                    }, 1500)
                    break;
            }
        },
        SOCKET_USER(state, user) {
            state.user = user

            if (user.unreadMessages.length != 0) {
                for (var i = 0; i < user.unreadMessages.length; i++) {
                    state.newMessages[user.unreadMessages[i]] = true
                }

            }


        },
        SOCKET_USERS(state, users) {

            state.users = users
            state.matched = []
            for (var i = 0; i < state.user.matched.length; i++) {

                var user1 = state.users.filter((user2) => {

                    return user2._id == state.user.matched[i]
                })

                state.matched.push(user1[0])
            }
        },
        SOCKET_TOKEN(state, token) {
           
            Bus.$emit('loginSuccess')
            localStorage.setItem('token', token)
        },
        SOCKET_NEWUSER(state, user) {
        
            state.users.push(user)

        },
        SOCKET_MATCHED(state, users) {

        
            if (state.user._id === users.user1._id) {
                state.user.matched.push(users.user2._id)
                state.matched.push(users.user2)
                var data = {
                    user: users.user1,
                    matched: users.user2,
                    isMatched: true
                }
                Bus.$emit('matched', data)
            }
            if (state.user._id === users.user2._id) {
                state.user.matched.push(users.user1._id)
                state.matched.push(users.user1)
                var data = {
                    user: users.user2,
                    matched: users.user1,
                    isMatched: true
                }
                Bus.$emit('matched', data)
            }
        },
        SOCKET_REGISTRATIONSUCCESSFUL(state) {
            Bus.$emit('regSuccess');
        },
        SOCKET_MESSAGES(state, data) {
            
            state.messages = data.messages
            setTimeout(() => {  
                Bus.$emit('messages' , data.length)
            }, 40)
        },
        SOCKET_NEWMESSAGE(state, message) {

            if (state.chatActive === message.to || state.chatActive === message.from) {
                state.messages.push(message)
            } else {
                state.newMessages[message.from] = true
                Bus.$emit('newMessageFromNotActive', message.from)
            }


            setTimeout(() => {
                Bus.$emit('messageSuccess')
            }, 40)

        },
        SOCKET_PREVIOUSMESSAGES(state,messages){
            for(var i= 0 ; i < messages.length ; i++){
                state.messages.push(messages[i])
            }
            Bus.$emit('prevMessageSuccess');
          
        }
    },
    actions: {
        getUsers(context) {
            context.commit('getUsers')
        },
        addUser(context, user) {
            context.commit('addUser', user)
        },
        login(context, user) {
            context.commit('login', user)
        },
        checkIfLoggedIn(context) {
            context.commit('checkIfLoggedIn')
        },
        likeUser(context, payload) {
            context.commit('likeUser', payload)
        }


    }
})


