<template>
    <div class="login-wrapper">
        <div v-show="!loginSuccess">
            <div class="logo">
                <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920385/logo_reb47h.svg" alt="" v-bind:class="error.hasError ? 'error' : ''">
                <h2 v-show="error.hasError">{{error.message}}</h2>
            </div>
            <div class="form">
                <div class="form-group">
                    <label for="username">Username</label>
                    <input type="text" v-model="username">
                </div>
                <div class="form-group">
                    <label for="password">Password</label>
                    <input type="password" v-model="password">
                </div>
                <br>
                <br>
                <div class="form-group">
                    <button @click="login" class="login">Login</button>

                </div>
                <div class="form-group">
                    <button @click="register" class="login">Register</button>
                </div>
            </div>
        </div>
        <div v-show="loginSuccess">

        </div>
    </div>
</template>
<script>
    import {
        Bus
    } from '../main'
    import {
        mapGetters
    } from 'vuex';
    export default {
        name: 'login',
        data() {
            return {
                username: '',
                password: '',
                loginSuccess: false
            }
        },
        computed: {
            ...mapGetters({
                error: 'loginError'
            })
        },
        methods: {
            login() {
                let user = {
                    username: this.username,
                    password: this.password
                }
                // this.$store.dispatch('login' , user )
                this.$socket.emit('login', user)
            },
            register() {
                this.$router.push('/registration')
            }
        },
        created() {
            if (localStorage.getItem('token')) {
                this.$socket.emit('verifyToken', localStorage.getItem('token'))
                if (this.$store.state.user) {
                    this.$router.push('/')
                }
            }

            Bus.$on('loginSuccess', () => {

                setTimeout(() => {
                    this.$router.push('/home')
                }, 1000)
            })
        }
    }
</script>
<style scoped>
    .login-wrapper {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #fff;
        overflow-y: scroll;
        overflow-x: hidden;
        max-width: 900px;
        text-align: middle;
    }

    .logo {
        position: absolute;
        margin: 0 auto;
        display: block;
        height: 40%;
        width: 100%;
    }

    .logo h2 {
        width: 100%;
        font-size: 0.9em;
        text-align: center;
        color: #e02100;
        padding-top: 100px;
    }

    .logo img {
        position: absolute;
        height: 50%;
        width: 50%;

        margin-left: -25%;
        left: 50%;
    }

    .form {
        position: absolute;
        width: 100%;
        height: 60%;
        top: 40%;
        padding-left: 2%;
    }

    .form-group {
        margin-top: 5%;
    }

    .form-group label {
        font-size: 0.8em;
        display: block;
        color: #e02100;
    }

    .form-group input {
        font-size: 1.5em;
        width: 95%;
        background-color: transparent;
        outline: none;
        border: none;
        border-bottom: 1px solid #e00;
    }

    .form-group button {
        font-size: 1em;
        width: 95%;
        padding: 2%;
        font-weight: bolder;
        text-transform: uppercase;
    }

    .login {
        background-color: #e02100;
        color: #fff;
        border: none;
    }

    .error {
        animation-name: shake;
        animation-duration: 0.1s;
    }

    @keyframes shake {
        0% {
            margin-left: -23%
        }
        50% {
            margin-left: -25%;
            margin-right: -27%
        }
        100% {
            margin-left: -23%
        }
    }
</style>



