<template>
    <div @click="getMessages(user._id ,  user.image)" class="img-box">
        <div class="img-wrapper">
            <img :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user.image" alt="" v-show="user.image">
            <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="!user.image">
        </div>
        <div class="dimple" v-show="hasUnreadMessages"></div>
    </div>
</template>
<script>
    import {
        Bus
    } from '../main'
    import {
        mapGetters
    } from 'vuex';

    export default {
        name: 'avatar',
        props: ['user'],
        data() {
            return {
                hasUnreadMessages: false
            }
        },
        methods: {
            getMessages: function (userId, userImg) {
                this.$store.state.messages = []
                for (var k in this.$store.state.newMessages) {
                    if (k === userId) {
                        delete this.$store.state.newMessages[userId]
                        this.hasUnreadMessages = false
                        this.$socket.emit('hasReadMessage', this.$store.state.user._id, userId)
                    }
                }


                Bus.$emit('getMessages', userId, userImg)
            }
        },
        computed: {
            ...mapGetters({
                newMessages: 'newMessages'

            }),

        },
        mounted() {
            for (var k in this.$store.state.newMessages) {
                if (k === this.user._id) {
                    this.hasUnreadMessages = true
                }
            }
            Bus.$on('hasReadMessages' , (userId , userImage)=>{
                this.getMessages(userId ,userImage)
            })
            Bus.$on('newMessageFromNotActive', (userId) => {
                if (userId === this.user._id) {
                    this.hasUnreadMessages = true
                }
            })
        }
    }
</script>
<style scoped>
    .img-box {
        display: inline-block;

        position: relative;
        height: 50px;
        width: 50px;
        margin-top: 10px;
        margin-left: 20px;
    }

    .img-wrapper {
        overflow: hidden;
        height: 50px;
        width: 50px;
        background-color: #eee;
        border-radius: 50%;
        box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5);
    }

    .img-wrapper img {
        width: 100%;
        display: block;
        /*for the img inside your div */
        margin-left: auto;
        margin-right: auto;
    }

    .dimple {
        position: absolute;
        right: 0;
        top: 0;
        background-color: #f49842;
        height: 15px;
        width: 15px;
        border-radius: 50%;
        box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5);
        border: 1px solid #fff;
    }
</style>



