<template>
    <div class="profile-wrapper" v-show="user" v-bind:class="[user ? 'slideUp' : '' , isClosing ? 'slideDown' : '']">
        <div class="name-box">
            {{user.name}}

            <button @click="closeProfile">
                <i class="fa fa-window-close fa-lg" aria-hidden="true"></i>
            </button>

        </div>
        <div class="img-box">
            <img v-if="user.image" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_80/' + user.image" alt="" v-show="user.image">
            <img src="http://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="!user.image">
        </div>
        <div class="details-box">
            <div class="description">
                <small>DESCRIPTION</small>
                <br>
                <br> {{user.description}}
            </div>
            <br>
            <br>
            <div style="border-bottom: 1px solid #eee"></div>

            <div class="other-details">
                <small>COURSE</small>
                <br>
                <br>
                <span style="text-transform : uppercase">{{user.course}}</span>
                <br>
                <br>
                <small>GENDER</small>
                <br>
                <br>
                <span style="text-transform : uppercase">{{user.gender}}</span>
                <br>
                <br>
                <small>INTERESTS</small>
                <br>
                <br>
                <span>{{user.interests}}</span>
            </div>
            <br>
            <br>
            <br>
            <br>
            <br>
            <br>


        </div>
        <button @click="sendMessage(user._id , user.image)" class="btn-send" v-show="client._id != user._id  && isMatched">
            <i class="fa fa-paper-plane fa-lg" aria-hidden="true"></i> Message</button>


        <div style="height: 200px;"></div>
    </div>
</template>
<script>
    import {
        Bus
    } from '../main'
    export default {
        name: 'profile',
        data() {
            return {
                isClosing: false,
                user: {},
                client: {},
                isMatched: false,
            }
        },
        props: [

        ],
        mounted: function () {



            Bus.$on('openProfile', user => {
                this.user = user
                this.isClosing = false
                this.isMatched = false
                for (var i = 0; i < this.$store.state.matched.length; i++) {

                    if (!this.isMatched) {
                        if (this.$store.state.matched[i]._id == this.user._id) {

                            this.isMatched = true
                        }
                    }

                }
            })

        },
        methods: {
            closeProfile: function () {
                this.isClosing = true
                setTimeout(function () {
                    Bus.$emit('closeProfile');
                }, 400);
            },
            sendMessage: (userId, userImage) => {

                this.isClosing = true
                setTimeout(function () {
                   
                    Bus.$emit('closeProfile');
                    Bus.$emit('closeProfiles');
                     Bus.$emit('hasReadMessages' , userId , userImage)
                    Bus.$emit('openMessage')
                }, 400)



            },
        }
    }
</script>
<style scoped>
    .profile-wrapper {
        position: fixed;
        height: 100%;
        width: 100%;
        overflow-y: scroll;
        overflow-x: hidden;
        z-index: 1100;
        top: 100%;
        background-color: #fff;
    }

    .img-box {
        display: block;
        width: 100%;
    }

    .img-box img {
        width: 100%;
    }

    .name-box {
        display: block;
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-left: 10px;
        font-size: 1.5em;
        text-transform: capitalize;
        font-weight: bold;
        letter-spacing: 1px;
        color: #f4a142;
    }

    .name-box button {
        background-color: transparent;
        border: none;
        position: absolute;
        right: 0;
        margin-right: 10px;
        top: 5px;
        font-size: 1em;
        color: #f4a142;
    }

    .description {
        padding-top: 10px;
        padding-left: 20px;
        width: 100%;
    }

    .other-details {
        padding-top: 10px;
        padding-left: 20px;
        width: 100%;
    }

    .btn-send {
        width: 80%;
        padding: 10px;
        display: block;
        margin: 0 auto;
        background-color: #f4a142;
        border: none;
        font-size: 1.2em;
        padding: 20px;
        color: #8c591e;
        box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5)
    }

    .slideUp {
        top: 100%;
        animation-name: slideUp;
        animation-duration: 0.2s;
        animation-fill-mode: forwards;
    }

    .slideDown {

        animation-name: slideDown;
        animation-duration: 0.4s;
    }

    small {
        font-size: 0.7em;
        letter-spacing: 1px;
        color: #f4a142;
    }

    @keyframes slideUp {
        from {
            top: 100%
        }
        to {
            top: 0
        }
    }

    @keyframes slideDown {
        from {
            top: 0%
        }
        to {
            top: 100%
        }
    }
</style>



