<template>
    <div class="match-wrapper">
        <div class="label">
            <span style="font-family: 'Dancing Script', cursive;font-size:2em;">It's a Match!</span>
            <br>
            <span style="font-size:1em;"> You and {{userMatch.name}} liked each other</span>
        </div>
        <div class="images">
            <div class="image-wrapper left">
                <img v-if="user.image" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_60,g_face,c_crop/'+ user.image" alt=""
                    v-show="user.image">
                <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="!user.image">
            </div>
            <div class="image-wrapper right">
                <img v-if="user.image" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_60,g_face,c_crop/' + userMatch.image" alt=""
                    v-show="userMatch.image">
                <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="!userMatch.image">
            </div>
        </div>
        <div class="buttons">
            <button @click="sendMessage(userMatch._id , userMatch.image)" id="message">Send Message</button>
            <button @click="closeMatch" id="swipe">Keep Swiping</button>
        </div>

    </div>
</template>
<script>
    import {
        Bus
    } from '../main'

    export default {
        name: 'matched',
        props: [
            'userMatch',
            'user'
        ],
        methods: {
            sendMessage: (userId, userImage) => {

                Bus.$emit('closeMatch');
                Bus.$emit('getMessages', userId, userImage)
                Bus.$emit('openMessage')



            },
            closeMatch: () => {


                Bus.$emit('closeMatch');
            }
        },
        created() {

        }
    }
</script>
<style scoped>
    .match-wrapper {
        z-index: 99999;
        position: fixed;
        background-color: rgba(0, 0, 0, 0.8);
        width: 100%;
        height: 100%;
        color: #fff;
    }

    .label {
        text-align: center;


        width: 100%;
        margin-top: 20%;
    }

    .images {
        width: 100%;
        text-align: center;
        padding-top: 10%;
    }

    .images img {
        width: 100%;
    }

    .image-wrapper {
        display: block;
        position: relative;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        overflow: hidden;
        background-color: #fff;
        border: 5px solid #fff;
    }

    .left {
        position: absolute;
        left: 18%;
    }

    .right {
        position: absolute;
        right: 18%;
    }

    .buttons {
        position: absolute;
        bottom: 10%;
        width: 100%;
        text-align: center;
        padding-left: 10%;
    }

    .buttons button {
        display: block;
        margin-top: 5%;
        margin-bottom: 5%;
        width: 80%;
        padding: 2%;

        font-family: 'Raleway', sans-serif;
        font-weight: bold;
        color: #fff;
        background-color: rgba(0, 0, 0, 0.6);
        outline: none;
        border: 2px solid #fff;
    }
</style>



