<template>
    <div class="single-message" >
        <div class="match-message day" style="color :#333;text-align:center;text-transform:uppercase;font-weight:bolder;font-size: 0.8em;padding-bottom:40px;"
            v-if="messages[0]._id != message._id && messages[keyId-1].day != message.day">
            <span>{{message.day}}</span>
        </div>
        <div class="chat-avatar" v-bind:class="[user._id === message.from ? 'user' : '']">

            <span v-show="user._id === message.from">
                <span>
                    <span v-if="messages[keyId + 1]">
                        <img alt="" v-if="messages[0]._id === message._id && messages[keyId + 1].from != user._id" v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user.image"
                        v-show="user.image">
                    </span>
                    <span v-if="!messages[keyId + 1]">
                        <img alt="" v-if="messages[0]._id === message._id && messages[keyId].from === user._id" v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user.image"
                        v-show="user.image">
                    </span>
                    <span v-if="messages[keyId + 1]">
                        <img alt="" v-if="messages[0]._id != message._id && messages[keyId + 1].from != user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user.image"
                        v-show="user.image">
                    </span>
                    <span v-if="!messages[keyId + 1]"> 
                         <img alt="" v-if="messages[0]._id != message._id && messages[keyId].from === user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + user.image"
                        v-show="user.image">
                    </span>
                </span>

            </span>
            <span v-show="user._id != message.from">
                <span v-if="messages[keyId+1]">
                    
                <img alt="" v-if="messages[0]._id === message._id && messages[keyId+1].from === user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + messageAvatar"
                    v-show="user.image">
                </span>
                <span v-if="!messages[keyId + 1]">
                    <img alt="" v-if="messages[0]._id === message._id && messages[keyId].from != user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + messageAvatar"
                    v-show="messageAvatar">
                </span>
                <span v-if="messages[keyId + 1]">
                     <img alt="" v-if="messages[0]._id != message._id && messages[keyId+1].from === user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + messageAvatar"
                    v-show="messageAvatar">
                </span>
                <span v-if="!messages[keyId + 1]">
                    <img alt="" v-if="messages[0]._id != message._id && messages[keyId].from != user._id " v-bind:src="'https://res.cloudinary.com/dxwtipc8n/image/upload/q_20,g_face,c_crop/' + messageAvatar"
                    v-show="messageAvatar">
                </span>
            </span>

            <img style="position:relative;margin-left: 5px;top:-20px;height:20px;width: 20px;" src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg"
                alt="" v-show="!user.image || !messageAvatar">
        </div>

        <span @click="timeToggle" class="line-message" v-bind:class="[user._id === message.from ? 'user message-user-bg' : '']">

            <small class="time" v-if="isShow"> {{message.time}}
                <br>
            </small>
            
            {{message.message}}

        </span>
    </div>
</template>
<script>
    export default {
        name: 'singlemessage',
        props: ['user', 'message', 'messages', 'keyId', 'messageAvatar'],
       
        data() {
            return {
                isShow: false,
                readyToshow: false,
                showMessage : false
            }
        },
        mounted() {
        
            if (this.user._id && this.message._id && this.messageAvatar) {
                this.readyToshow = true

            }
            setTimeout(()=>{
                this.showMessage = true
            } , 500)
            
           
        },
        methods: {
            timeToggle: function () {
                this.isShow = !this.isShow
                setTimeout(() => {
                    this.isShow = false
                }, 3000)
            }
        }
    }
</script>
<style scoped>
    .single-message {
        position: relative;
        display: block;
        clear: both;
        margin-top: 10px;
        margin-bottom: 10px;
        bottom: 0;
        
        animation-name : fadein;
        animation-duration : 1s;
        animation-fill-mode: forwards;
        
   
    }

    .time {
        font-size: 0.7em;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
    }

    .chat-avatar {
        overflow: hidden;
        height: 30px;
        width: 30px;
        border-radius: 50%;
        display: inline-block;
        position: relative;
        vertical-align: top;
        top: 8px;
    }
    .day{
        color: #aaa;
        text-align: center;
        text-transform: uppercase;
        font-weight: bolder;
        font-size: 0.8em;
        margin-bottom: 20px;
        padding-bottom: 20px;
        margin-left: -5px;
        padding-left: 10px;
        letter-spacing: 2px;
        border-bottom: 1px solid #eee;
    }
    .chat-avatar img {

        width: 100%;
    }

    .match-message {
        display: block;
        width: 99%;
        word-wrap: break-word;
        clear: both;
        padding: 5px;
        vertical-align: text-top;
       
    }

    .line-message {
        background-color: #eee;
        padding: 10px;
        padding-left: 10px;
        padding-right: 10px;
        max-width: 60%;
        border-radius: 20px;
        position: relative;
        margin-right: 10px;
        margin-left: 10px;
        font-size: 0.9em;
        font-weight: 600;
        display: inline-block;
        color: #333;
        
        transition-property: width, height;
        
    }

    .user {
        float: right;
        color: #fff;
        margin-right: 10px;
        margin-top: 5px;
    }

    .message-user-bg {
        background-color: #44c4ff;
    }
    .fadeIn{
        animation-name : fadein;
        animation-duration : 1s;
        animation-fill-mode: forwards;
    }
    @keyframes fadein {
        0%{opacity: 0}
        100%{opacity: 1}
    }
</style>



