<template>
    <div class="control-wrapper">


        <i @click="openMessage" class="fa fa-envelope  fa-2x" aria-hidden="true"></i>


    </div>
</template>
<script>
    import {
        Bus
    } from '../main'
    export default {
        name: 'control',
        data() {
            return {
                isCancel: false,
                isHeart: false
            }
        },
        methods: {
            cancelClicked() {
                this.isCancel = true
                let userId = this.$store.state.users[0]._id

                Bus.$emit('swipeLeft', userId)
                setTimeout(() => {
                    this.isCancel = false
                }, 100)
            },
            openMessage() {
                Bus.$emit('openMessage')

            },
            heartClicked() {
                this.isHeart = true
                setTimeout(() => {
                    this.isHeart = false
                }, 100)
            }
        }
    }
</script>
<style scoped>
    .control-wrapper {
        position: absolute;
        bottom: 10px;

        margin: 0 auto;
        width: 100%;

        height: 80px;
        font-size: 1.5em;
        color: #e02100;
        text-align: center;
    }

    .control-wrapper i {
        padding: 15px;
        background-color: #fff;
        vertical-align: middle;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
        border-radius: 50%;
    }

    .control-btn {
        position: absolute;
        background-color: #fff;
        width: 180px;
        height: 180px;
        border-radius: 50%;
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.2);
        transition: all 0.1s ease-in-out;
    }

    .control-btn-img {
        position: absolute;
        margin: 0 auto;
        left: 15%;
        top: 15%;
        height: 70%;
    }

    .cancel {
        left: 23%;
        top: 8%;
    }

    .heart {
        right: 23%;
        top: 8%;
    }

    .shrink {
        animation-name: shrink;
        animation-duration: 0.2s;
    }

    @keyframes shrink {
        0% {
            transform: scale(1)
        }
        100% {
            transform: scale(0.8)
        }
    }
</style>



