<template>

    <v-touch @tap="openProfile(user)" @swipeleft="notMatch" @swiperight="match">
        <div class="user-card" v-bind:class="[isNotMatch ? 'swipeLeft' : '' , isMatch ? 'swipeRight' : '' ]">
            <img v-if="user.image" :src="'https://res.cloudinary.com/dxwtipc8n/image/upload/h_700,w_800,c_fill,q_60/' + user.image" alt="" v-show="user.image">
            <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="!user.image">

            <div class="user-description">
                <h6>{{user.name}} </h6>
                <h5>{{user.course}}</h5>
            </div>
        </div>
    </v-touch>

</template>
<script>
    import draggable from 'vuedraggable'
    import {
        Bus
    } from '../main'
    export default {
        name: 'userCard',
        data() {
            return {
                isNotMatch: false,
                isMatch: false
            }
        },
        props: [
            'user'
        ],
        components: {
            draggable

        },
        methods: {
            openProfile(user) {
                Bus.$emit('openProfile' , user )
            },
            notMatch() {
                this.isNotMatch = true
                Bus.$emit('swipe')


            },
            match() {
                this.isMatch = true;
                var data = {
                    userId: this.$store.state.user._id,
                    likeId: this.user._id
                }
                Bus.$emit('swipe')
                this.$socket.emit('liked', data)

            },
            cardMove(e) {
                e.preventDefault();

            }
        }
    }
</script>
<style scoped>
    v-touch:first-child {
        box-shadow: 0px 0px 30px rgba(0, 0, 0, 1);
    }

    .user-card {
        position: absolute;
        margin-left: 1%;
        margin-right: 1%;
        height: 98%;
        width: 98%;
        z-index: 10;
        background-color: #fff;
        border-radius: 30px;
        overflow: hidden;

        transition: all 0.1s ease-in-out;
        vertical-align: middle;
        text-align: center;
    }

    .swipeLeft {
        animation-name: swipeLeft;
        animation-duration: 0.4s;

        animation-fill-mode: forwards;
    }

    .swipeRight {
        animation-name: swipeRight;
        animation-duration: 0.4s;

        animation-fill-mode: forwards;
    }

    .user-card img {
        margin-right: auto;
        margin-left: auto;
        height: 100%;
        
        display: block;
        background-color: #fff;
    }

    .user-description {
        background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
        position: absolute;
        bottom: -10px;
        height: 20%;
        width: 100%;
        text-shadow: 0px 2px 10px rgba(0, 0, 0, 0.5);
        padding: 20px;
    }

    .user-description h6 {
        position: absolute;
        bottom: 0;
        font-size: 1.5em;
        margin-top: 12%;
        color: #fff;
        text-transform: capitalize;
    }

    .user-description h5 {
        position: absolute;
        text-transform: uppercase;
        bottom: 0px;
        margin-top: 14%;
        font-size: 1em;
        font-weight: 100;
        left: 20px;
        color: #fff;
    }

    v-touch {
        position: absolute;
        height: 100%;
        width: 100%;
    }

    @keyframes swipeLeft {
        0% {
            left: 0
        }
        100% {
            left: -150%;
            transform: rotate(30deg)
        }
    }

    @keyframes swipeRight {
        0% {
            left: 0
        }
        100% {
            left: 150%;
            transform: rotate(-30deg)
        }
    }
</style>



