<template>
    <div class="registration-wrapper">

        <div v-show="!isTerms">
            <label for="pic-upload" class="pic-upload">
                <img src="https://res.cloudinary.com/dxwtipc8n/image/upload/v1509920658/male-avatar_illsec.svg" alt="" v-show="image === ''">
                <img :src="image" v-show="image != ''">
                <b v-show="image === ''"> click avatar to upload a picture</b>
            </label>
            <input id="pic-upload" type="file" @change="onFileChange" accept="image/*">
            <div class="details">
                <label for="username">Username</label>
                <input type="text" id="username" v-model="username">
                <label for="password">Password</label>
                <input type="password" id="password" v-model="password">
                <label for="password1">Re-type Password</label>
                <input type="password" id="password1" v-model="password1">
                <label for="name" style="text-transform:capitalize">Name</label>
                <input type="text" id="name" v-model="name">
                <label for="gender">Gender</label>
                <select name="gender" id="gender" v-model="gender">
                    <option value="male">male</option>
                    <option value="female">female</option>

                </select>
                <label for="course">Course</label>
                <input type="text" id="course" style="text-transform:uppercase" v-model="course">
                <label for="description">Description</label>
                <textarea name="description" id="description" cols="30" rows="10" placeholder="tell me something about yourself" v-model="description"></textarea>
                <label for="interested">Interested in:</label>
                <select name="interested" id="" v-model="interests">
                    <option value="men">men</option>
                    <option value="women">women</option>
                    <option value="both">both</option>
                </select>
                <button @click="showTerms">Register</button>
                <button @click="closeRegister" style="background-color :#f78c00">Cancel</button>
            </div>
        </div>

        <div class="terms" v-show="isTerms">
            <div class="terms-details">
                <h3> Terms and Conditions</h3>
                <div>
                    <b>THIS IS APP IS FOR DEMONSTRATION ONLY!!!</b>
                    <br>
                    <br>
                    <b>Itinder</b> is created by
                    <b>Reniel Santiago</b>. Built to demonstrate web capabilities and show users the latest web technologies
                    <br>
                    <br> -this app is not for commercial use. any copyright infringement is not admissible and cannot be used
                    as evidence of any violation of the law in any country
                    <br>
                    <br>
                    <b>Itinder</b> is owned by
                    <b>Reniel Santiago</b> and him only. Any organization, group , company , or any entity is not entitled in
                    ownership rights to this app . Therefore , ownership of the app belongs to the owner and the owner only.
                    <br>
                    <br>
                    <b>FOR THE USERS :</b>
                    <br>
                    <br> The user is responsible for the data that they have saved within the app. Any violation of the law in
                    any country regarding the data is the responsibility of the user.
                    <br>
                    <br> Any data within the app cannot be used as evidence in any violation of the law in any country .
                    <br>
                    <br> By accepting the terms and conditions, you hereby accept and understand all the conditions that is written
                    within the agreement and will not be forced to accept this agreement.

                </div>
                <br>
                <br>
                <div class="term-btns" v-show="!isRegistering">
                    <button @click="register">Accept and Register</button>
                    <button @click="closeTerms">Cancel</button>

                </div>
                <div class="term-btns" style="width:100%;text-align:center" v-show="isRegistering">
                    <i class="fa fa-circle-o-notch fa-spin fa-3x fa-fw"></i>
                    <span class="sr-only">Loading...</span>
                </div>
                <div style="height:  100px;"></div>
            </div>

        </div>
        <div class="errors" v-show="hasError">
            {{errorMessage}}
        </div>
    </div>
</template>
<script>
    import axios from 'axios'
    import {
        url2
    } from '../main'
    import {
        url1
    } from '../main'
    import {
        Bus
    } from '../main'
    export default {
        name: 'registration',
        data() {
            return {
                image: '',
                imageFacade: '',
                name: '',
                course: '',
                interests: 'men',
                description: '',
                username: '',
                password: '',
                password1: '',
                gender: '',
                isTerms: false,
                isRegistering: false,
                hasError: false,
                errorMessage: ''
            }
        },
        methods: {
            closeRegister() {
                this.$router.push('/login')
            },
            onFileChange(e) {
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length)
                    return;
                this.createImage(files[0]);

                this.imageFacade = files[0].name;

            },
            createImage(file) {
                var image = new Image();
                var reader = new FileReader();
                var vm = this;

                reader.onload = (e) => {
                    vm.image = e.target.result;
                };
                reader.readAsDataURL(file);

            },
            showTerms() {
                if (this.username === '') {
                    this.hasError = true
                    this.errorMessage = 'username required'
                }

                if (this.hasError) {
                    setTimeout(() => {
                        this.hasError = false
                        this.errorMessage = ''
                    }, 1500)
                } else {
                    this.$socket.emit('checkIfUsername', this.username)
                }



            },
            closeTerms() {
                this.isTerms = false
            },
            register() {
                let payload = {
                    image: this.image,
                    name: this.name,
                    course: this.course,
                    gender: this.gender,
                    description: this.description,
                    interests: this.interests,
                    username: this.username,
                    password: this.password

                }
                this.isRegistering = true
                this.$socket.emit('register', payload)


            }
        },
        sockets: {
            verifiedUsername() {
                if (this.course === '') {
                    this.hasError = true
                    this.errorMessage = 'course required'
                }
                if (this.gender === '') {
                    this.hasError = true
                    this.errorMessage = 'gender required'
                }


                if (this.name === '') {
                    this.hasError = true
                    this.errorMessage = "name is required"
                }
                if (this.password != this.password1) {
                    this.hasError = true
                    this.errorMessage = "passwords do not match"
                }
                if (this.password.length < 6) {
                    this.hasError = true
                    this.errorMessage = "short password"
                }


                if (this.hasError) {
                    setTimeout(() => {
                        this.hasError = false
                        this.errorMessage = ''
                    }, 1500)
                } else {
                    this.isTerms = true
                }



            },
            errorUsername() {
                this.hasError = true
                this.errorMessage = 'username already exists'
                setTimeout(() => {
                    this.hasError = false
                    this.errorMessage = ''
                }, 1500)
            }
        },
        mounted() {
            Bus.$on('regSuccess', () => {
                this.$router.push('/home')
            })

        }
    }
</script>
<style scoped>
    .registration-wrapper {
        position: relative;
        width: 100%;
        overflow-x: hidden;
        height: 100%;
        z-index: 1000;
    }

    .registration-wrapper input[type="file"] {
        font-size: 1em;
        display: none;
    }

    .pic-upload {
        width: 90%;
        display: block;
        margin: 0 auto;
        background-color: #efefef;
    }

    .pic-upload img {
        width: 100%;
        display: block;
        margin: 0 auto;
        margin-top: 10px;
        border: 1px solid #999;
    }

    .pic-upload b {
        top: -100px;
        position: relative;
        text-align: center;
        font-size: 1em;
        display: block;
    }

    .details {
        padding: 20px;
        position: relative;
        width: 100%;
        padding-bottom: 300px;
        background-color: #fff;
    }

    .details label {
        display: block;
        font-size: 0.8em;
        color: #8f8f8f;
        margin-top: 10px;
    }

    .details input {
        display: block;
        margin-top: 5px;
        width: 90%;

        font-size: 1.5em;
        outline: none;
        border: none;
        border-bottom: 1px solid #999;
    }

    .details textarea {

        margin-top: 50px;
        width: 90%;
        font-size: 1em;
    }

    select {
        display: block;
        margin-top: 50px;
        width: 90%;
        text-transform: capitalize;
        font-size: 1em;
        outline: none;
        border: none;
        border-bottom: 1px solid #999;
    }

    .details button {
        font-size: 1em;
        display: block;
        margin-top: 20px;
        width: 90%;
        height: 50px;
        background-color: #31ad0f;
        color: #fff;
        font-weight: bold;
        outline: none;
        border: none;
    }

    .terms {
        position: fixed;
        z-index: 99;
        height: 100%;
        width: 100%;
        background-color: #fff;
        top: 0;
        left: 0;
    }

    .terms-details {
        position: relative;
        height: 100%;
        width: 90%;
        font-size: 1em;
        padding: 20px;
        padding-top: 0;
        overflow-x: hidden;
        overflow-y: scroll;
    }

    .terms-details h3 {
        font-size: 1.3em;
        margin-left: -20px;
        width: 100%;
        display: block;
        border-bottom: 1px solid #ccc;
        padding: 20px;
    }

    .terms-btns {
        display: block;
        padding-top: 40px;
    }

    .term-btns button {

        margin: 10px;
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;
        margin-left: 0;
        border: none;
        font-size: 1em;
    }

    .errors {
        position: fixed;
        color: #fff;
        width: 100%;
        height: 40px;
        bottom: 0;
        background-color: #e02100;
        font-weight: bold;
        padding: 10px;
    }
</style>



